body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgba(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.container {
  width: 100%;
}

h1 {
  text-transform: uppercase;
  text-align: center;
  color: #ffff;
  font-size: 50px;
}

.container button {
  margin: 15px auto;
  border: none;
  padding: 15px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  cursor: pointer;
  color: #ffff;
  background-color: dodgerblue;
  transition: 0.3s;
  font-size: 16px;
}

.container button:hover {
  background-color: rgb(23, 85, 148);
}

.robofriends {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  flex-shrink: 2;
  padding-bottom: 50px;
  padding-top: 30px;
}

@media (max-width: 600px) {
  .robofriends {
    flex-direction: column;
    flex-wrap: nowrap;
    width: 90%;
    margin: 0 auto;
  }
}

.robofriends_card {
  width: 17%;
  background-color: lightgreen;
  text-align: center;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
}

@media (max-width: 600px) {
  .robofriends_card {
    width: 100%;
  }
}

.robofriends_card:hover {
  transform: scale(1.07);
  cursor: pointer;
}

.robofriends_card img {
  width: 200px;
  height: 200px;
}

.searchBox {
  width: 20%;
  margin: 0 auto;
  padding-bottom: 15px;
  text-align: center;
}

.searchBox input {
  width: 100%;
  padding: 10px 0 10px 0;
  border: none;
  border-radius: 5px;
  outline: none;
  background-color: beige;
}

.searchBox input::-webkit-input-placeholder {
  padding-left: 10px;
}
